import React from "react";
import { Button, Form, Input } from "antd";

const App = () => {
  const [form] = Form.useForm();

  return (
    <Form
      name="wrap"
      labelCol={{ flex: "110px" }}
      labelAlign="left"
      labelWrap
      wrapperCol={{ flex: 1 }}
      colon={false}
      style={{ maxWidth: 600 }}
      form={form}
    >
      <Form.Item
        label="Normal label"
        name="username"
        rules={[{ required: true }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="A super long label text"
        name="password"
        rules={[{ required: true }]}
      >
        <Input />
      </Form.Item>

      <Form.Item label=" ">
        <Button
          type="primary"
          onClick={() => {
            form.validateFields();
          }}
        >
          Submit
        </Button>
      </Form.Item>
    </Form>
  );
};

export default App;
